
$primary: #00FFA7;


$warning: #FFFE54;
$title: 'white';
$backgroundColor: #14141e;
$backgroundColorSecondary: #1E252C;

$border: #0A9A6D;

body {
  background-color: $backgroundColor;
}

.header-container{
  
 margin-bottom: 20px;
}
.header {
  background-color: $backgroundColorSecondary;
  height: 140px;
  padding-left: 50px;
  padding-top: 20px;
 

  .title{
    font-family: Orbitron;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    color: $primary;

  }

  .menu-liner{
    padding-top: 15px;
  }

  
}


.liner-side{
  width: 20%;
  height: 4px;
  background-color:#0A9A6D;
  float: left;
}

.liner-mid{
  margin-left: 1%;
  margin-right: 1%;
  width: 58%;
  height: 3px;
  background-color: #105143;
  float: left;
}


.text{

  font-family: Teko;
  font-style: normal;
  font-weight: 500;
  font-size: 25px;




  &.label{
    color: #FFFFFF;
    padding-right: 15px;
  }
  &.value{
    color: $primary;
    padding-right: 50px;
  }
  &.warning{
    color: $warning;
    padding-right: 50px;
  }


}


.card{

 border: 20px solid transparent;
  padding: 15px;
  border-image: url('./Assets/card.svg');


  background-color: $backgroundColor;
  border-image-slice: 10% 10%;

}

.inner-card{

  border: 2px solid  $border;
  box-sizing: border-box;
  border-radius: 16px;
  padding: 10px 10px;
  margin-bottom: 24px;
}

.right-dashboard{
  min-height: 100px;

}


.widget-container{
  min-height: 150px ;
}

.map{
  height: 70%;
  width: 100%;
  overflow-block: 'hidden';


  .cover{
    position: 'absolute';
    height:60px;
    width: 100px;
    background-color: $warning;
    top: 10;
    left: 100;

  }
}



.social-media{
  min-height: 120px;
}


a[href^="http://maps.google.com/maps"]{display:none !important}

.gmnoprint a, .gmnoprint span {
    display:none;
}
.gmnoprint div {
    background:none !important;
}